import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    battles: [],
    challengers: new Set()
  },
  mutations: {
    setBattles(state, payload) {
      payload.battles.map(battle => {
        state.challengers.add(battle.item_1.name);
        state.challengers.add(battle.item_2.name);
      });
      state.battles = payload.battles;
    },
    newBattle(state, payload) {
      state.battles = [...state.battles, payload.battle];
      state.challengers.add(payload.battle.item_1.name);
      state.challengers.add(payload.battle.item_2.name);
    },
    upvote(state, payload) {
      const selectedBattle = state.battles[payload.index];
      switch (payload.item) {
        case 1:
          selectedBattle.item_1.vote += 1;
          break;
        case 2:
          selectedBattle.item_2.vote += 1;
          break;
      }
    },
    setChallengers(state, payload) {
      payload.challengers.map(challenger => {
        state.challengers.add(challenger);
      });
    }
  },
  actions: {},
  modules: {}
});
